import React, {useState, useEffect} from 'react'
import ExperienceDetail from './ExperienceDetail'
import ExperienceFilter from './ExperienceFilter'
import ExperienceCreateWrapper from './ExperienceCreateWrapper'
import APICalls from '../../../APICalls.js'

/*
State: data, loading, url

Effect: call refresh data, call again every time url changes. Pass setData to the function so data is updated from API

rendering: if loading, render loading div. 
If data exists, render a list of Detail components. Pass the individual data item, refresh function and setLoading function as props
Render the Create Form component, with refresh and setLoading as props
Render the Filter Form component, with setURL and setLoading as props

*/
				
function ExperienceControl(props) {
	let [data, setData] = useState([])
	let [loading, setLoading] = useState(true)
	let [url, setUrl] = useState(process.env.REACT_APP_API_URL + 'drf_api/vse/Experience/')
	
	useEffect(() => {
		APICalls.refreshData(setLoading, setData, url)
	}, [url])
	
	let loadingDiv = null
	if (loading) {
		loadingDiv = <div className='loading-small'><h1>Loading...</h1></div>
	}
	
	let details
	if (data && data.length !== 0){
		details = data.map((experience) => {
			return (
					<ExperienceDetail 
						key={experience.id}
						experience={experience} 
						setLoading = {setLoading}
						refresh = {() => APICalls.refreshData(setLoading, setData, url)}
					/>
			)
		})
	}
	
	return(
		<div>
			{loadingDiv}
			<div className='col border border-light rounded p-2 px-3 mb-2 mt-1 controls'>
				<div className="row">
					<div className="col my-2">
						<ExperienceCreateWrapper 
							setLoading={setLoading} 
							refresh={() => APICalls.refreshData(setLoading, setData, url) }
						/>
					</div>
				</div>
		
				<div className="row">
					<div className="col my-2">
						<ExperienceFilter setUrl={setUrl} setLoading={setLoading}/>
					</div>
				</div>
				
				<div className="row">
					<div className='col-auto mb-1 mt-2'>
						<button onClick={()=>{APICalls.refreshData(setLoading, setData, url)}} className="btn btn-secondary">Refresh</button>
					</div>
				</div>
			</div>
				
			<div className="row">
				{details}
			</div>
		</div>
	)
}

export default ExperienceControl