import React from 'react'
import ValueControl from './Values/ValueControl.js'
import SkillControl from './Skills/SkillControl.js'
import ExperienceControl from './Experiences/ExperienceControl.js'
import DescriptionText from './DescriptionText.js'

function CRUDTab(props) {
	return(
		<div>
			<DescriptionText />
			
			<nav>
			  <div className="nav nav-tabs" id="nav-tab" role="tablist">
				<a className="nav-item nav-link active tab-controls border-bottom" id="nav-value-tab" data-toggle="tab" href="#nav-value" role="tab" aria-controls="nav-value" aria-selected="true">Values</a>
				<a className="nav-item nav-link tab-controls border-bottom" id="nav-skill-tab" data-toggle="tab" href="#nav-skill" role="tab" aria-controls="nav-skill" aria-selected="false">Skills</a>
				<a className="nav-item nav-link tab-controls border-bottom" id="nav-experience-tab" data-toggle="tab" href="#nav-experience" role="tab" aria-controls="nav-experience" aria-selected="false">Experiences</a>
			  </div>
			</nav>
			
			<div className="tab-content" id="nav-tabContent">
			  <div className="tab-pane fade show active" id="nav-value" role="tabpanel" aria-labelledby="nav-value-tab">
				<ValueControl />
			  </div>
			  
			  <div className="tab-pane fade" id="nav-skill" role="tabpanel" aria-labelledby="nav-skill-tab">
				<SkillControl />
			  </div>
				
			  <div className="tab-pane fade" id="nav-experience" role="tabpanel" aria-labelledby="nav-experience-tab">
				<ExperienceControl />
			  </div>
			</div>
		</div>
	)
}

export default CRUDTab