import React, {useState, useEffect} from 'react'
import {Formik, Form, Field, ErrorMessage } from 'formik'
import APICalls from '../../../APICalls.js'

/*
State: updateMessage, formOptions 

useEffect: makes an API call to refresh data for formOptions

Update: call setLoading from props. Sends form data to API and stores message. Calls refresh and setLoading again from props 

Renders a form with validation. The form has initial values passed from data object, passed from the Detail
*/

function validate(values) {
	const errors = {}
	
	if(!values.name) {
		errors.name = 'Required'
	}
	
	return errors
}

function ExperienceUpdate(props) {
	let [updateMessage, setUpdateMessage] = useState(null)
	let [values, setValues] = useState(null)
	let [skills, setSkills] = useState(null)
	
	useEffect(() => {
		let valueUrl = process.env.REACT_APP_API_URL + 'drf_api/vse/Value/'
		let skillUrl = process.env.REACT_APP_API_URL + 'drf_api/vse/Skill/'
		
		APICalls.refreshData(props.setLoading, setValues, valueUrl)
		APICalls.refreshData(props.setLoading, setSkills, skillUrl)
	}, [props.setLoading])
	
	async function refreshForm() {
		APICalls.refreshData(props.setLoading, setValues, process.env.REACT_APP_API_URL + 'drf_api/vse/Value/')
		APICalls.refreshData(props.setLoading, setSkills, process.env.REACT_APP_API_URL + 'drf_api/vse/Skill/')
		setUpdateMessage(null)
	}
	
	async function experienceUpdate(formData) {
		//Takes in form data, sends it to create new entry, returns server response
		props.setLoading(true)
		
		let refreshToken = await localStorage.getItem('refreshToken')
		let accessToken = await APICalls.getAccessToken(refreshToken)
		let updateResponse = await APICalls.update(accessToken, formData, props.experience.url)
		
		setUpdateMessage(updateResponse)
		props.refresh()
		props.setLoading(false)
	}
	
	let valueOptions
	if (values && values.length > 0){
		valueOptions = values.map((val) => {
			return (
				<option value={val.url} key={val.id}>
					{val.name}
				</option>
			)
		})
	}
	
	let skillOptions
	if (skills && skills.length > 0){
		skillOptions = skills.map((skl) => {
			return (
				<option value={skl.url} key={skl.id}>
					{skl.name}
				</option>
			)
		})
	}
	
	return (
		<Formik
			initialValues= {{
				name: props.experience.name,
				value_set: props.experience.value_set,
				skill_set: props.experience.skill_set,
			}}
			
			validate={validate}
			
			onSubmit= {(values, {setSubmitting})=>{
				experienceUpdate(values)
			}}
		>
			<Form>
				<h4>Update Experience</h4>
				
				<div className='form-group'>
					<label htmlFor="name">Experience Name: </label>
					<Field name="name" type="text" className='form-control'/>
					<div className="text-danger"><ErrorMessage name="name"/></div>
				</div>
				
				<div className="form-group">
					<label htmlFor="value_set">Values: </label>
					<Field as="select" name='value_set' multiple className='form-control'>
						{valueOptions}
					</Field>
					<div className="text-danger"><ErrorMessage name="value_set"/></div>
				</div>
				
				<div className="form-group">
					<label htmlFor="skill_set">Skills: </label>
					<Field as="select" name='skill_set' multiple className='form-control'>
						{skillOptions}
					</Field>
					<div className="text-danger"><ErrorMessage name="skill_set"/></div>
				</div>
				
				<button type="submit" className='btn btn-primary'>Update</button>
				<button type="reset" className='btn btn-secondary' onClick={refreshForm}>Refresh Form</button>
				
				{updateMessage}
			</Form>
		</Formik>
	)
}

export default ExperienceUpdate