import React from 'react'
import {Formik, Form, Field, ErrorMessage } from 'formik'

/*
Renders a login form with validation. Login function is passed from LoginControl as a prop.
*/

function validate(values) {
	const errors = {}
	
	if (!values.username) {
		errors.username = "Required"
	}
	
	if (!values.password) {
		errors.password = "Required"
	}
	
	return errors
}

function LoginForm(props) {
	return (
		<Formik
			initialValues= {{
				username: '',
				password: '',
			}}
			
			validate={validate}
			
			onSubmit= {(values, {setSubmitting})=>{
				props.login(values)
			}}
		>
			<Form>
				<h2>Login</h2>
				{props.loginErrorMessage}
				
				<div className="form-group">
					<label htmlFor="username">Username: </label>
					<Field name="username" type="text" className="form-control"/>
					<div className='text-danger'><ErrorMessage name="username"/></div>
				</div>
				
				<div className="form-group">
					<label htmlFor="password">Password: </label>
					<Field name="password" type="password" className="form-control"/>
					<div className='text-danger'><ErrorMessage name="password"/></div>
				</div>
				
				<button type="submit" className="btn btn-primary">Login</button>
			</Form>
		</Formik>
	)
}

export default LoginForm