import React, {useState} from 'react'
import {Formik, Form, Field, ErrorMessage } from 'formik'
import APICalls from '../APICalls'

/*
Handles all API calls on its own, but takes in a refresh prop from LoginControl

State: loading, updateMessage

Only handles update of password, not username
update: setLoading true, makes an API call to get current user's url, send form data to user url, receives message, saves message to state. Set loading to false.

Rendering: if loading, render a loading div. If disabled, return only the signup message, without the form.
*/

function validate(values) {
	const errors = {}
	
	if (!values.password) {
		errors.password = "Required"
	} else if (values.password.length < 8) {
		errors.password = "Password needs to be at least 8 characters"
	}
	
	if (!values.password2) {
		errors.password2 = "Required"
	} else if (values.password2 !== values.password) {
		errors.password2 = "Passwords do not match"
	}
	
	return errors
}

function PasswordUpdateForm(props) {
	let [loading, setLoading] = useState(false)
	let [updateMessage, setUpdateMessage]= useState(null)
	
	async function update(updateFormData) {
		setLoading(true)
		
		let refreshToken = await localStorage.getItem('refreshToken')
		let accessToken = await APICalls.getAccessToken(refreshToken)
		
		let userData = await APICalls.getUser(accessToken)
		console.log(updateFormData)
		let updateResponse = await APICalls.update(accessToken, updateFormData, userData.url)
		
		props.refresh()
		setUpdateMessage(updateResponse)
		setLoading(false)
	}
	
	let loadingDiv
	if (loading) {
		loadingDiv = <div><div>Loading...</div></div>
	}
	
	return (
		<Formik
			initialValues= {{
				password: '',
				password2: '',
			}}
			
			validate={validate}
			
			onSubmit= {(values, {setSubmitting, resetForm})=>{
				update(values)
				resetForm()
			}}
		>
			<Form>
				{loadingDiv}
				<h4>Update Password</h4>

				<div className="form-group">				
					<label htmlFor="password">Password: </label>
					<Field name="password" type="password" id="signup_password" className="form-control"/>
					<div className='text-danger'><ErrorMessage name="password"/></div>
				</div>
				
				<div className="form-group">
					<label htmlFor="password2">Confirm Password: </label>
					<Field name="password2" type="password" className="form-control"/>
					<div className='text-danger'><ErrorMessage name="password2"/></div>
				</div>
								
				<button type="submit" className='btn btn-primary'>Update</button>
				{updateMessage}
				
			</Form>
		</Formik>
	)
}

export default PasswordUpdateForm