import React, {useState} from 'react'
import {Formik, Form, Field, ErrorMessage } from 'formik'
import APICalls from '../APICalls'

/*
Standalone component rendered by LoginControl, without taking any props. Handles all API calls on its own

State: loading, enable, signupMessage

signup: setLoading true, send form data to API, receives message, saves message to state. If successful, setEnable to false. 
Set loading to false.

Rendering: if loading, render a loading div. If disabled, return only the signup message, without the form.
*/

function validate(values) {
	const errors = {}
	
	if (!values.username) {
		errors.username = "Required"
	}
	
	if (!values.password) {
		errors.password = "Required"
	} else if (values.password.length < 8) {
		errors.password = "Password needs to be at least 8 characters"
	}
	
	if (!values.password2) {
		errors.password2 = "Required"
	} else if (values.password2 !== values.password) {
		errors.password2 = "Passwords do not match"
	}
	
	return errors
}

function SignupForm(props) {
	let [loading, setLoading] = useState(false)
	let [enable, setEnable] = useState(true)
	let [signupMessage, setSignupMessage]= useState(null)
	
	async function signup(signupFormData) {
		//Takes in form data, sends form data to signup URL. If successful: returns success message. Else, return failure message
		setLoading(true)
		
		//Sends form data to token pair link. If successful, stores refresh token as cookie, access token to variable
		let response = await APICalls.signupMessage(signupFormData)
		
		if (response === "Signup successful. Please log in.") {
			setEnable(false)
		}	
		
		setSignupMessage(response)
		setLoading(false)
	}
	
	let loadingDiv
	if (loading) {
		loadingDiv = <div><div>Loading...</div></div>
	}
	
	if (!enable) {
		return(
			<div>
				{loadingDiv}
				{signupMessage}
			</div>
		)
	}
	
	return (
		<Formik
			initialValues= {{
				username: '',
				password: '',
				password2: '',
			}}
			
			validate={validate}
			
			onSubmit= {(values, {setSubmitting})=>{
				signup(values)
			}}
		>
			<Form>
				{loadingDiv}
				<h3>Signup</h3>
				
				<div className="form-group">
					<label htmlFor="username">Username: </label>
					<Field name="username" type="text" id="signup_username" className="form-control"/>
					<div className='text-danger'><ErrorMessage name="username"/></div>
				</div>
							
				<div className="form-group">				
					<label htmlFor="password">Password: </label>
					<Field name="password" type="password" id="signup_password" className="form-control"/>
					<div className='text-danger'><ErrorMessage name="password"/></div>
				</div>
				
				<div className="form-group">
					<label htmlFor="password2">Confirm Password: </label>
					<Field name="password2" type="password" className="form-control"/>
					<div className='text-danger'><ErrorMessage name="password2"/></div>
				</div>
								
				<button type="submit" className='btn btn-primary'>Sign Up</button>
				{signupMessage}
				
			</Form>
		</Formik>
	)
}

export default SignupForm