import React from 'react'
import ExperienceUpdateWrapper from './ExperienceUpdateWrapper'
import ExperienceDelete from './ExperienceDelete'

/*
State: showUpdateForm

Rendering: receives a data object as a prop, and renders all its properties
Renders an Update form component if showUpdateForm is true. A button toggles showUpdateForm
Renders a Delete component

*/

function ExperienceDetail(props) {
	let values
	if (props.experience.value_names && props.experience.value_names.length > 0) {
		values = props.experience.value_names.map((val, index) => {
			return <li key={index}>{val}</li>
		})
	}
	
	let skills
	if (props.experience.skill_names && props.experience.skill_names.length > 0) {
		skills = props.experience.skill_names.map((skl, index) => {
			return <li key={index}>{skl}</li>
		})
	}
	
	return(
		<div className='col-12 col-md-6 col-lg-4 p-2'>
		<div className='border border-light rounded h-100 p-4 detail'>
			<h3>{props.experience.name}</h3>
			<h4>Values</h4>
			<ul>
				{values}
			</ul>
			
			<h4>Skills</h4>
			<ul>
				{skills}
			</ul>
			
			<ExperienceUpdateWrapper 
				experience={props.experience} 
				refresh={props.refresh} 
				setLoading={props.setLoading}
			/>	
			
			<div className='row my-2'>
				<div className='col'>
					<ExperienceDelete 
						experience={props.experience} 
						refresh={props.refresh} 
						setLoading={props.setLoading}
					/>
				</div>
			</div>

		</div>
		</div>
	)
}

export default ExperienceDetail