import React, {useState} from 'react'
import {Formik, Form, Field } from 'formik'

/*
Filter: uses form data to construct a new URL. Then calls setLoading and setUrl which are passed from props.

Renders a form with validation
*/

//Takes in setLoading and setUrl from parent
function ExperienceFilter(props) {
	let [showForm, setShowForm] = useState(false)
	
	async function experienceFilter(formData) {
		//Takes in form data, creates URL with get parameters, calls setLoading and setUrl
		props.setLoading(true)
		
		let url=process.env.REACT_APP_API_URL + 'drf_api/vse/Experience/?'
		
		if (formData.experienceFilterName) {
			url += ('experienceFilterName=' + formData.experienceFilterName + '&')
		}
		
		props.setUrl(url)
		props.setLoading(false)
	}
	
	let filterForm
	if (showForm){
		filterForm = (
		<Formik
			initialValues= {{
				experienceFilterName: '',
			}}
			
			onSubmit= {(values, {setSubmitting})=>{
				experienceFilter(values)
			}}
		>
			<Form>
				<div className="form-group">
					<label htmlFor="experienceFilterName">Experience Name: </label>
					<Field name="experienceFilterName" type="text" className='form-control'/>
				</div>
				
				<button type="submit" className='btn btn-primary mr-1'>Filter</button>
				<button type="reset" onClick={() => props.setUrl(process.env.REACT_APP_API_URL + 'drf_api/vse/Experience/?')} className='btn btn-danger'>Reset</button>
			</Form>
		</Formik>
		)
	}
	
	return (
		<div>
			<div className='row'>
				<div className='col'>
					<h4>Filter Experience</h4>
				</div>
				<div className='col-auto'>
					<button onClick={()=>{setShowForm(prev=>!prev)}} className="btn btn-primary">Show</button>
				</div>
			</div>
			<div>
				{filterForm}
			</div>
		</div>
		
	)
}

export default ExperienceFilter