import React from 'react'

function DescriptionText() {
	return(
		<div className='px-4 py-2'>
			<h4>Welcome!</h4>
			<p className='px-4 py-2'>Hello! Welcome to VSE app. This app allows you to track what values and skills you have, as well as what experiences you can use to show them off. This can be a tool to help you prepare for interviews or just know more about yourself. The 3 tabs show the 3 main fields, for Values, Skills and Experiences. Every value and skill can be linked to multiple experiences, while every experience can  be linked to multiple skills and values too.</p>
			
			<h4>Starting out</h4>
			<p className='px-4 py-2'>Start by thinking about what skills and values you have or that employers are looking for. Then, use the starter forms on the Skills and Values tab to create multiple skills and values all at once.</p>
			<p className='px-4 py-2'>Then, move to the Experience tab. Think about what life experiences you have that can demonstrate your skills and values. Then, use the create form to start adding experiences. Select which of the skills and experiences you can link your experience to. Click the refresh form button to see the values and experiences you have added earlier.</p>
			
			<h4>Building your list</h4>
			<p className='px-4 py-2'>With some starter skills, values and experiences, you can continue building your list. Each tab allows you to create new entries, as well as update existing ones.</p>
			
		</div>
	)
}

export default DescriptionText