import React from 'react'
import SkillUpdateWrapper from './SkillUpdateWrapper'
import SkillDelete from './SkillDelete'

/*
State: showUpdateForm

Rendering: receives a data object as a prop, and renders all its properties
Renders an Update form component if showUpdateForm is true. A button toggles showUpdateForm
Renders a Delete component

*/

function SkillDetail(props) {
	let experiences
	if (props.skill.experience_names && props.skill.experience_names.length > 0) {
		experiences = props.skill.experience_names.map((expName, index) => {
			return <li key={index}>{expName}</li>
		})
	}
	
	return(
		<div className='col-12 col-md-6 col-lg-4 p-2'>
		<div className='border border-light rounded h-100 p-4 detail'>
			<h3>{props.skill.name}</h3>
			<h4>Experiences</h4>
			<ul>
				{experiences}
			</ul>
			
			<SkillUpdateWrapper 
				skill={props.skill} 
				refresh={props.refresh} 
				setLoading={props.setLoading}
			/>	
			
			<div className='row my-2'>
				<div className='col'>
					<SkillDelete 
						skill={props.skill} 
						refresh={props.refresh} 
						setLoading={props.setLoading}
					/>
				</div>
			</div>

		</div>
		</div>
	)
}

export default SkillDetail