import React, {useState} from 'react'
import SkillUpdate from './SkillUpdate'

function SkillUpdateWrapper(props) {
	let [showUpdateForm, setShowUpdateForm] = useState(false)
	
	let updateForm
	if (showUpdateForm) {
		updateForm = (
			<div className='row mt-2'>
				<div className='col'>
					<SkillUpdate
						skill={props.skill} 
						refresh={props.refresh} 
						setLoading={props.setLoading}
					/>
				</div>
			</div>
		)
	}
	
	return(
		<div>	
			<div className='row'>
				<div className='col'>
					<button onClick={()=> setShowUpdateForm(prevValue => !prevValue)} className="btn btn-info">Update</button>
				</div>
			</div>
			
			{updateForm}
		</div>
	)
}

export default SkillUpdateWrapper