import React, {useState, useEffect} from 'react'
import LoginForm from './LoginForm'
import SignupForm from './SignupForm'
import CRUDTab from './CRUD/CRUDTab'
import UserDetailModal from './UserDetailModal'
import APICalls from '../APICalls'

/*
Has 3 state variables: username, loading and loginErrorMessage

Effect: runs only at start of app. Checks cookies for a valid refresh token. If valid, update username to current logged in user.

Login: takes in login form data and sends it to server. If successful: save refresh token obtained. Use access token to get
user data and update username. Else: save error message and render it.

Logout: removes data from storage and sets username to null

Rendering: if loading, a loading div will be created. If no username, it will render the login/signup components. If username, it will
render the main page.

*/

function LoginControl() {
	const [username, setUsername] = useState(null)
	const [loading, setLoading] = useState(true)
	const [loginErrorMessage, setLoginErrorMessage] = useState(null)
	
	useEffect(()=>{
		async function initialUsername() {
			setLoading(true)
			let refreshToken = await localStorage.getItem('refreshToken')
			let accessToken = await APICalls.getAccessToken(refreshToken)
			let userData = await APICalls.getUser(accessToken)
			
			setUsername((userData ? userData.username : null)) 
			setLoading(false)
		}
		
		initialUsername()
	}, [])
	
	async function refresh() {
		setLoading(true)
		let refreshToken = await localStorage.getItem('refreshToken')
		let accessToken = await APICalls.getAccessToken(refreshToken)
		let userData = await APICalls.getUser(accessToken)
		
		setUsername((userData ? userData.username : null)) 
		setLoading(false)
	}
	
	async function login(loginFormData) {
		setLoading(true)
		let accessToken = await APICalls.loginAccessToken(loginFormData)

		//If access token was obtained, obtains user data
		let currentUsername
		let loginErrorMessage
		
		if (accessToken) {
			let userData = await APICalls.getUser(accessToken)
			currentUsername = (userData ? userData.username : null)
			loginErrorMessage = null
		} else {
			currentUsername = null
			loginErrorMessage = (<p className="text-danger">Username or password incorrect</p>)
		}
		
		setUsername(currentUsername)
		setLoginErrorMessage(loginErrorMessage)
		setLoading(false)
	}
	
	async function logout() {
		setLoading(true)
		localStorage.removeItem('refreshToken')
		setUsername(null)
		setLoading(false)
	}
	
	let loadingDiv
	if (loading) {
		loadingDiv = <div className='loading'><h1>Loading...</h1></div>
	}
	
	if (username){
		return (
			<div>
				{loadingDiv}
				<div className="row my-2">
					<div className="col">
						<p>Hello {username}!</p>
					</div>
					<div className="col-auto">
						<UserDetailModal 
							refresh = {refresh}
							setLoading = {setLoading}
						/>
					</div>
					<div className="col-auto">
						<button onClick={logout} className='btn btn-secondary'>Logout</button>
					</div>
				</div>
				
				<div className="row">
					<div className="col">
						<CRUDTab/>
					</div>
				</div>
			</div>
		)
	}
	
	return(
		<div>
			{loadingDiv}
			
			<div className='row'>
				<div className='col-lg-8 col-xl-10 mx-auto p-2'> 
					<p>This app allows you to track what skills and values you have, as well as what experiences you have to show them off. Use this to prepare for interviews or understand yourself better. Create an account below to get started.</p>
					<p>Warning! Do not reuse any passwords for this app!</p>
				</div>
			</div>
			
			<div className="row">
				<div className='col-xs-12 col-lg-8 border border-light rounded my-2 mx-auto p-4 controls'> 
					<LoginForm login={login} loginErrorMessage={loginErrorMessage} />
				</div>
			</div>
			
			<div className="row">
				<div className='col-xs-12 col-lg-8 border border-light rounded my-2 mx-auto p-4 controls'> 
					<SignupForm />
				</div>
			</div>
		</div>
	)
	
	
}

export default LoginControl