import React, {useState} from 'react'
import APICalls from '../../../APICalls.js'

/*
State: confirmDelete

Delete: calls setLoading from props. Sends delete request to URL obtained from props. Calls refresh and setLoading from props

Rendering: a button toggles value of confirmDelete. If confirmDelete is true, render the confirm delete button. 
The confirm delete button calls Delete on click.
*/

function ExperienceDelete(props){
	let [confirmDelete, setConfirmDelete] = useState(false)
	
	async function experienceDelete() {
		//Sends delete response to server, returns server response
		props.setLoading(true)
		
		let refreshToken = await localStorage.getItem('refreshToken')
		let accessToken = await APICalls.getAccessToken(refreshToken)
		await APICalls.delete(accessToken, props.experience.url)
		
		props.refresh()
		props.setLoading(false)
	}
	
	let confirmDeletePrompt
	if (confirmDelete) {
		confirmDeletePrompt = (
			<div className='row'>
				<div className='col pt-3'>
					<p>Are you sure you want to delete {props.experience.name}?</p>
					<button onClick={experienceDelete} className='btn btn-danger'>Confirm Delete</button>
				</div>
			</div>
		)
	} 
	
	
	return(
		<div>
			<div className='row'>
				<div className='col'>
					<button onClick={()=>setConfirmDelete( prevValue => !prevValue)} className='btn btn-info'>Delete</button>
				</div>
			</div>
			
			{confirmDeletePrompt}
		</div>
	)
}

export default ExperienceDelete