import React from 'react'
import UsernameUpdateForm from './UsernameUpdateForm'
import PasswordUpdateForm from './PasswordUpdateForm'
import UserDelete from './UserDelete'

function UserDetailModal(props) {
	return(
		<div>
			<button type="button" className="btn btn-info" data-toggle="modal" data-target="#userModal">
			  Edit User
			</button>

			<div className="modal fade" id="userModal" tabIndex="-1" role="dialog" aria-labelledby="userModalLabel" aria-hidden="true">
			  <div className="modal-dialog" role="document">
				<div className="modal-content custom-modal">
				  
					  <div className="modal-header">
						<h3 className="modal-title" id="userModalLabel">Edit User</h3>
						<button type="button" className="close" data-dismiss="modal" aria-label="Close">
						  <span aria-hidden="true">&times;</span>
						</button>
					  </div>
					  
					  <div className="modal-body">
						<div className='row mb-4'><div className="col">
							<UsernameUpdateForm 
								refresh = {props.refresh}
							/>
						</div></div>
						
						<div className='row my-4'><div className="col">
							<PasswordUpdateForm 
								refresh = {props.refresh}
							/>
						</div></div>
						
						<div className='row mt-4'><div className="col">
							<UserDelete
								refresh = {props.refresh}
								setLoading = {props.setLoading}
							/>
						</div></div>
					  </div>
					  
					  <div className="modal-footer">
						<button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
					  </div>
				 
				</div>
			  </div>
			</div>
			
		</div>
	)
}

export default UserDetailModal