import React, {useState} from 'react'
import {Formik, Form, Field } from 'formik'
import APICalls from '../../../APICalls.js'

/*
State: createMessage, formOptions 

Create: call setLoading from props. Sends form data to API and stores message. Calls refresh and setLoading again from props 

Renders a form with validation
*/

function ValueMultipleCreate(props) {
	let [createMessage, setCreateMessage] = useState(null)
	
	async function valueCreate(formData) {
		//Takes in form data, sends it to create new entry, returns server response
		props.setLoading(true)

		let formData2 = []
		let formValues = Object.values(formData)
		formValues.forEach(name => {
			if (name) {
				formData2.push({
					name: name,
					experiences: []
				})
			}
		})
		
		let url = process.env.REACT_APP_API_URL + 'drf_api/vse/Value/'
		
		let refreshToken = await localStorage.getItem('refreshToken')
		let accessToken = await APICalls.getAccessToken(refreshToken)
		let createResponse = await APICalls.create(accessToken, formData2, url)
		
		setCreateMessage(createResponse)
		props.refresh()
		props.setLoading(false)
	}
	
	let formControls = []
	for (let i=1; i<=6; i++) {
		formControls.push(
			<div className="col-md-6 col-lg-4" key={i}><div className="form-group">
				<label htmlFor={"name" + i}>Value {i}: </label>
				<Field name={"name" + i} type="text" className='form-control'/>
			</div></div>
		)
	}
	
	return (
		<div className="col">
			<Formik
				initialValues= {{
					name1: '',
					name2: '',
					name3: '',
					name4: '',
					name5: '',
					name6: '',
				}}
				
				onSubmit= {(values, {setSubmitting})=>{
					valueCreate(values)
				}}
			>
				<Form>
					<div className="row">
						{formControls}
					</div>
					
					<button type="submit" className='btn btn-primary mr-1'>Create</button>
					<button type="reset" className='btn btn-secondary'>Reset Form</button>
					
					{createMessage}
				</Form>
			</Formik>
		</div>
	)
}

export default ValueMultipleCreate