import React, {useState, useEffect} from 'react'
import {Formik, Form, Field, ErrorMessage } from 'formik'
import APICalls from '../../../APICalls.js'

/*
State: updateMessage, formOptions 

useEffect: makes an API call to refresh data for formOptions

Update: call setLoading from props. Sends form data to API and stores message. Calls refresh and setLoading again from props 

Renders a form with validation. The form has initial values passed from data object, passed from the Detail
*/

function validate(values) {
	const errors = {}
	
	if(!values.name) {
		errors.name = 'Required'
	}
	
	return errors
}

function SkillUpdate(props) {
	let [updateMessage, setUpdateMessage] = useState(null)
	let [experiences, setExperiences] = useState([])
	
	useEffect(() => {
		let url = process.env.REACT_APP_API_URL + 'drf_api/vse/Experience/'
		APICalls.refreshData(props.setLoading, setExperiences, url)
	}, [props.setLoading])
	
	async function refreshForm() {
		APICalls.refreshData(props.setLoading, setExperiences, process.env.REACT_APP_API_URL + 'drf_api/vse/Experience/')
		setUpdateMessage(null)
	}
	
	async function skillUpdate(formData) {
		//Takes in form data, sends it to create new entry, returns server response
		props.setLoading(true)
		
		let refreshToken = await localStorage.getItem('refreshToken')
		let accessToken = await APICalls.getAccessToken(refreshToken)
		let updateResponse = await APICalls.update(accessToken, formData, props.skill.url)
		
		setUpdateMessage(updateResponse)
		props.refresh()
		props.setLoading(false)
	}
	
	let options
	if (experiences && experiences.length > 0){
		options = experiences.map((exp) => {
			return (
				<option value={exp.url} key={exp.id}>
					{exp.name}
				</option>
			)
		})
	}
	
	return (
		<Formik
			initialValues= {{
				name: props.skill.name,
				experiences: props.skill.experiences,
			}}
			
			validate={validate}
			
			onSubmit= {(values, {setSubmitting})=>{
				skillUpdate(values)
			}}
		>
			<Form>
				<h4>Update Skill</h4>
				
				<div className='form-group'>
					<label htmlFor="name">Skill Name: </label>
					<Field name="name" type="text" className='form-control'/>
					<div className="text-danger"><ErrorMessage name="name"/></div>
				</div>
				
				<div className='form-group'>
					<label htmlFor="experiences">Experiences: </label>
					<Field as="select" name='experiences' multiple className='form-control'>
						{options}
					</Field>
					<div className="text-danger"><ErrorMessage name="experiences"/></div>
				</div>
				
				<button type="submit" className='btn btn-primary'>Update</button>
				<button type="reset" className='btn btn-secondary' onClick={refreshForm}>Refresh Form</button>
				
				{updateMessage}
			</Form>
		</Formik>
	)
}

export default SkillUpdate