import React, {useState} from 'react'
import SkillCreate from './SkillCreate'

function SkillCreateWrapper(props) {
	let [showForm, setShowForm] = useState(false)
	
	let innerForm = null
	if (showForm) {
		innerForm = (
			<SkillCreate 
				setLoading={props.setLoading} 
				refresh={props.refresh}
			/>
		)
	}
	
	return (
		<div>
			<div className='row'>
				<div className='col'>
					<h4>Create Skill</h4>
				</div>
				<div className='col-auto'>
					<button onClick={()=>{setShowForm(prev=>!prev)}} className="btn btn-primary">Show</button>
				</div>
			</div>
			
			
			{innerForm}
		</div>
	)
}

export default SkillCreateWrapper