
/*

This is a collection of functions that handle all API call behaviour. All functions only take in inputs like a URL,
access token or form data. The functions handle the API call and the server response. The functions return a response,
which can be another token or a success/error message directly.

These methods are used inside the React components. State updating functions will be done inside each component itself 

Exception: the refresh data method expects the setLoading and setData methods from a React Hook. This is so the code is 
reuseable, as it will be called by multiple displays and forms.

*/
const APICalls = {
	getAccessToken: async (refreshToken) => {
		let url = process.env.REACT_APP_API_URL + 'drf_api/token_refresh/'
		let access = await fetch(url, {
			method: 'POST',
			headers: {'Content-Type': 'application/json'},
			body: JSON.stringify( {refresh: refreshToken} )
		})
		.then(res=> res.json())
		.then(data=> (data.access ? data.access : null))
		.catch(err => {
			alert('Something went wrong while checking for refresh token')
		})
		return access
	},
		
	getData: async (accessToken, url) => {
		let results

		if (accessToken) {
			results = await fetch(url, {
				headers: {'Authorization': `Bearer ${accessToken}`},
			})
			.then(res=> res.json())
			.then(data=> (data.length > 0 ? data : null))
			.catch(err => {
				alert('Something went wrong while checking for data')
			})
		}
		
		return results
	},
	
	refreshData: async (setLoading, setData, url) => {
		setLoading(true)
		let refreshToken = await localStorage.getItem('refreshToken')
		let accessToken = await APICalls.getAccessToken(refreshToken)
		let values = await APICalls.getData(accessToken, url)
		setData(values)
		setLoading(false)
	},
	
	getUser: async (accessToken) => {
		let user

		if (accessToken) {
			user = await fetch(process.env.REACT_APP_API_URL + 'drf_api/user/', {
				headers: {'Authorization': `Bearer ${accessToken}`},
			})
			.then(res=> res.json())
			.then(data=> (data.username ? data : null))
			.catch(err => {
				alert('Something went wrong while checking for data')
			})
		}
		
		return user
	},
	
	loginAccessToken: async(loginFormData) => {
		//Sends form data to token pair link. If successful, stores refresh token as cookie, access token to variable
		let loginUrl = process.env.REACT_APP_API_URL + 'drf_api/token_pair/'
		let accessToken = await fetch(loginUrl, {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify( loginFormData )
        })
		.then(res=> res.json())
		.then(data=> {
			if (data.refresh) {
				localStorage.setItem('refreshToken', data.refresh)
				return data.access
			} else {
				return null
			}
		}).catch(err => {
			alert('Something went wrong while checking for refresh token')
		})
		
		return accessToken
	},
	
	signupMessage: async(signupFormData) => {
		let signUpUrl = process.env.REACT_APP_API_URL + 'drf_api/signup/'
		let signUpMessage = await fetch(signUpUrl, {
			method: 'POST',
			headers: {'Content-Type': 'application/json'},
			body: JSON.stringify( signupFormData )
		})
		.then(res=> {
			if (res.status === 400) {
				return res.json()
			} else {
				return {username: 'ok'}
			}
		})
		.then(data => {
			if (data.username === 'ok') {
				return "Signup successful. Please log in."
			} else {
				let errorMessage = ''
				
				if (String(data.username) === 'This field must be unique.') {
					errorMessage = "Username already taken. "
				}
				
				if (data.password) {
					errorMessage += data.password.join(" ")
				}

				return errorMessage
			}
		})
		
		return signUpMessage
	},
	
	create: async(accessToken, createFormData, createUrl) => {
		let createResponse = await fetch(createUrl, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': `Bearer ${accessToken}`
			},
			body: JSON.stringify( createFormData )
		})
		.then(res=> {
			if (res.status === 201) {
				return "Create Successful"
			} else {
				return "Error Occurred. Please refresh the page."
			}
		})
		
		
		return createResponse
	},
	
	update: async(accessToken, updateFormData, updateUrl) => {
		let updateResponse = await fetch(updateUrl, {
			method: 'PUT',
			headers: {
				'Content-Type': 'application/json',
				'Authorization': `Bearer ${accessToken}`
			},
			body: JSON.stringify( updateFormData )
		})
		.then(res=> {
			if (res.status === 200) {
				return "Update Successful"
			} else {
				return "Error Occurred. Please refresh the page."
			}
		})
		
		
		return updateResponse
	},
	
	delete: async(accessToken, deleteUrl) => {
		let deleteResponse = await fetch(deleteUrl, {
			method: 'DELETE',
			headers: {'Authorization': `Bearer ${accessToken}`},
		})
		.then(res=> {
			if (res.status === 204) {
				return "Deletion Successful"
			} else {
				return "Error Occurred. Please refresh the page."
			}
		})
		
		
		return deleteResponse
	}
}

export default APICalls